/* The switch - the box around the slider */
.switch[data-v-3bd00680] {
  display: inline-block;
  position: relative;
  height: 34px;
  overflow: hidden;
  width: 70px;
}

/* Hide default HTML checkbox */
.switch input[data-v-3bd00680] {
    display:none;
}
.switch-off[data-v-3bd00680] {
    padding: 8px 0px 6px 0px;
    position: absolute;
    right: 5px;
    transition-property: all;
    transition-duration: .4s;
    -webkit-transition-duration: .4s;
}
.switch-off-active[data-v-3bd00680] {
    right: -32px;
}
.switch-on[data-v-3bd00680] {
    left: -25px;
    padding: 8px 0px 6px 0px;
    position: absolute;
    transition-property: all;
    transition-duration: .4s;
    -webkit-transition-duration: .4s;
}
.switch-on-active[data-v-3bd00680] {
    left: 5px;
}

/* The slider */
.slider[data-v-3bd00680] {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 600ms;
}
.slider[data-v-3bd00680]:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}
input:checked + .slider[data-v-3bd00680] {
  background-color: #2196F3;
}
input:focus + .slider[data-v-3bd00680] {
  box-shadow: 0 0 1px #2196F3;
}
input:checked + .slider[data-v-3bd00680]:before {
  -webkit-transform: translateX(36px);
  transform: translateX(36px);
}

/* Rounded sliders */
.slider.round[data-v-3bd00680] {
  border-radius: 34px;
}
.slider.round[data-v-3bd00680]:before {
  border-radius: 50%;
}
